.footer{
    font-size: 19px;
    
}
.footerUpperSection{
    padding-bottom : 140px ;
    width : 90%;
    margin :auto;
    display  :flex;
  
    padding: 80px;

}
.footer1stSection{
    flex-direction: column;
    width  :30%;

}
.footer2ndSection{
    flex : 1;
    display :flex;
    flex-wrap: wrap;
    justify-content: space-between;
}
.footer .communityIcons {
    display: flex;
    gap: 10px;
}

.footer .iconLink {
    color: white;
    font-size: 18px;
    transition: color 0.3s;
    position: relative;
}
