.boldHeading{
    font-weight: 400;
    font-size : 20px;
    padding : 10px 0;
   
}
.iconLink{
    color : white;
}
.sectionLink{
    color : gray;
    padding: 5px;
    
}

.sectionLink:hover{
    color : white;
}

.footer1stSection{
    display :flex;
    justify-content: center;
    align-items: center;
}
.botttomFooter{

background: linear-gradient(180deg, #131313 -28.18%, #000000 67.42%);
display :flex;
justify-content: center;
align-items: center;
position:relative;
height : 101px;
position : relative;
font-size: 16px;
}
.footer2ndSection a{
    color: gray;
    text-decoration: none;
    display: block;
}
.iconImage{

    width : 25px;
}
.footer1stSection{
 

   justify-content: center;
   align-items: flex-start;
   padding: 10px;
   
}
@media screen and (max-width: 1080px) {
    .footer{
        
        
    }

    .footerUpperSection{
        padding: 0;
        display :flex;
        width : 100%;
        flex-direction: column;
        gap : 30px;
        padding : 40px;
        padding-top: 140px;
        border-top: 1px solid gray;

    }
    .footer2ndSection {
        gap : 20px;      
    }
    .botttomFooter{
        padding : 25px;
    }
}

@media only screen and (max-width: 650px) {
    .footerUpperSection{
        padding: 40px 0;
    }
    .footer2ndSection{
        justify-content: space-around;
    }
    .footer2ndSection a{
       font-size: 16px;
       margin: 8px 0;
    }
    .footer-box{
        width: 45%;
    }
    .footer1stSection .logo img{
        width: 60px !important;
    }
    .footer1stSection{
        padding: 0 10px;
    }
}